import { Typography } from "@material-ui/core";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import restImg from "../../img/restaurant.png";

const useStyles = makeStyles({
  restInfo: {
    marginTop: "40px",
  },
  main_text: {
    textAlign: "center",
    fontWeight: "600",
    color: "#3d4152",
    textTransform: "uppercase",
    opacity: 0.9,
    fontSize: "1.2rem",
    marginTop: "9px",
  },
  light_text: {
    fontSize: "0.93rem",
    color: "#535665",
    textAlign: "center",
    opacity: 0.6,
    textOverflow: "ellipsis",
    whiteSpace: "nowrap",
    overflow: "hidden",
  },
  icon_img_div: {
    textAlign: "center",
  },
  icon_img: {
    width: "60px",
    padding: "4px",
    border: "1px solid lightgray",
    borderRadius: "7px",
    background: "#f7f2f2",
  },
});

const Menu = (props) => {
  const classes = useStyles();
  return (
    <div>
      <div className={classes.restInfo}>
        <div className={classes.icon_img_div}>
          <img
            className={classes.icon_img}
            src={restImg}
            alt="Restaurant Icon"
          />
        </div>
        <Typography className={classes.main_text}>Crunhy Bites!</Typography>
        <Typography className={classes.light_text}>
          {["Indian", "Italian"].join(", ")}
        </Typography>
        <div></div>
      </div>
      <div className="menu"></div>
    </div>
  );
};

export default Menu;
